  top: 27px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}
.custom-file-container__custom-file__custom-file-control:before {
  content: "Minimum size of 300x300 jpeg ipg png 5 MB";
  color: #c2c2c4;
  top: 45px;
  font-size: 12px;
  background: transparent;
  border: none;
  line-height: 1.2;
}
.add-photo {
  background: #f7f7f7;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  color: #7c8695;
  width: 100%;
  height: 100%;
  text-align: center;
  padding: 9px 11px 0 11px;
  display: none;
}
.add-photo__description {
  color: #c2c2c4;
  font-size: 12px;
}
.add-photo__error {
  font-size: 12px;
  color: #fb6363;
}
.custom-file-container__custom-file__custom-file-control:empty::after {
  content: "Add photo";
  color: #7c8695;
}
.custom-file-container__image-clear {
  position: absolute;
  top: -19px;
  right: -8px;
  z-index: 1;
}
.custom-file-container__custom-file {
  height: 130px;
  width: 130px;
  margin-top: 0;
}
.custom-file-container {
  margin-top: 3px;
  width: 130px;
  z-index: 1;
}
.btn_close_form {
  width: 24px;
  height: 24px;
  background: #65bade;
  border-radius: 50%;
  cursor: pointer;
}
.btn_close_form:before,
.btn_close_form:after {
  height: 12px;
  left: 11px;
  top: 6px;
}
.btn_close_form:hover {
  background: #d95252;
}
.custom-file-container__custom-file__custom-file-input {
  min-width: 130px;
  height: 130px;
  width: 130px;
}
/* **************** ABOUT ****************** */
.about h2,
.about .h2 {
  text-align: center;
  font-weight: 300;
  margin-bottom: 40px;
}
.about__wrap {
  max-width: 1280px;
  margin: 0 auto;
}
.about__wrap .left-sidebar__footer {
  display: flex;
  justify-content: space-between;
  padding-bottom: 16px;
}
.about__text {
  display: flex;
  justify-content: space-between;
  padding-bottom: 110px;
  position: relative;
}
.about__text .text__item {
  width: 47%;
  line-height: 1.5;
}
.divider {
  width: 100%;
  height: 1px;
  background: #e0e2e6;
  margin-bottom: 32px;
}
/* **************** MEDIA ****************** */
@media only screen and (max-width: 970px) {
  .left-sidebar,
  .right-sidebar {
    display: none;
  }
  .page-header {
    position: relative;
  }
  .page-header .profile-member {
    display: none;
  }
  .page-header .navigation {
    margin-right: 42px;
  }
  .mobile_btn {
    display: block;
  }
}
@media only screen and (max-width: 768px) {
  .page-header #search {
    order: 1;
    width: 100%;
    padding-top: 13px;
  }
  .page-header #search .search {
    width: 100%;
  }
  .page-header .page-header__container {
    flex-wrap: wrap;
  }
  .h2,
  h2 {
    font-size: 26px;
    margin-bottom: 8px;
  }
  .service-directory__item {
    width: 110px;
  }
  .service-directory__item .item__icon {
    width: 110px;
    height: 110px;
  }
  .about__wrap,
  .form__wrap {
    padding: 10px;
    box-sizing: border-box;
  }
  .form__wrap {
    margin-bottom: 30px;
  }
  .about__text {
    padding-bottom: 25px;
    flex-direction: column;
  }
  .about__text .text__item {
    width: 100%;
  }
  .header__log-in {
    padding-bottom: 156px;
  }
  .header__log-in .top-line {
    margin-bottom: 47px;
  }
}
@media only screen and (max-width: 468px) {
  .service-directory__header {
    flex-direction: column;
    align-items: center;
  }
  .btn-add {
    padding: 7px 22px;
  }
  .h1,
  h1 {
    font-size: 42px;
  }
  .about__wrap .left-sidebar__footer {
    flex-direction: column;
  }
  .header__log-in .top-line {
    padding: 0 25px;
  }
}
